.ItemSelector {
  border: 1px solid $color-inputborder;

  .button.dropdown-icon{
    width: 0px;
    background-color: unset;
  }
  .button.dropdown-icon.x{
    width: 2em;
    background: url(graphics/x-field.svg) no-repeat;// scroll 1em 1em;
    background-origin: content-box;
    background-position: center center;
    background-size: 1em;
    cursor: pointer;
  
  }

  .button.dropdown-icon.x:hover{
    color: green;
  }
}

.react-autosuggest__input, input{
  font-family: $font-serif;
  font-size: 18px;
}

select, option{
  // font-family: $font-sans;
  font-size: 14px;
}

.react-autosuggest__input,
input,
select {
  text-align: left;
  box-sizing: border-box;
  width: 100%;

  background-color: $color-backgroundgray;
  color: $color-yaleblack;
  border: 1px solid $color-inputborder;
  // padding: 10px 15px;
  height: 36px;
  padding: 0px 4px;
}

.TextInput input,
.react-autosuggest__input {
  padding-left: 10px;
  background: url(graphics/search-icon.svg) no-repeat scroll 7px 7px;
  background-origin: content-box;
  background-position: center right;
  background-size: 21px;
  background-color: $color-backgroundgray;
}

